.container {
  max-width: 900px;
  margin: 28px auto;
  padding: 16px;
  border-radius: 24px;
  background: #fcf8ff;
  box-shadow: 0 2px 16px rgba(60,0,150,0.09);
}

.quote-box {
  border-radius: 16px;
  background: #c5e6f7;
  padding: 18px;
  text-align: center;
  font-size: 1.3em;
  margin-bottom: 22px;
}

.streak-counter {
  display: flex;
  align-items: center;
  background: #f9e2e7;
  border-radius: 18px;
  padding: 16px;
  font-size: 1.5em;
  justify-content: center;
  margin-bottom: 18px;
}

.big-emoji {
  font-size: 2em;
  margin-right: 12px;
}

.streak-num {
  font-size: 2.5em;
  margin: 0 12px;
  font-weight: bold;
  color: #f04d23;
}

.calendar-todo {
  display: flex;
  gap: 28px;
  align-items: flex-start;
}

.calendar-card {
  background: #fff;
  border-radius: 24px;
  box-shadow: 0 5px 24px #a87d6330;
  padding: 22px 20px 14px 20px;
  max-width: 320px;
  margin: 26px auto;
}

.calendar-header-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4px;
}
.month-title {
  font-size: 1.3em;
  font-weight: bold;
  color: #51412b;
}
.arrow-btn {
  background: #efe8e3;
  border: none;
  border-radius: 50%;
  width: 34px;
  height: 34px;
  font-size: 1.2em;
  color: #7d6b51;
  cursor: pointer;
}
.calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  margin-bottom: 9px;
}
.weekday {
  color: #89ac6b;
  text-align: center;
  font-weight: bold;
  font-size: .95em;
}
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
  margin-bottom: 16px;
}

.empty {
  background: transparent;
  pointer-events: none;
}
/* --- Transparent Colors --- */
.calendar-box-streak {
  background: rgba(255, 140, 0, 0.25);
  color: #333;
}

.calendar-box-complete {
  background: rgba(80, 200, 120, 0.25);
  color: #333;
}

.calendar-box-incomplete {
  background: rgba(255, 60, 60, 0.25);
  color: #333;
}

.calendar-box-today {
  background: rgba(52, 152, 255, 0.30);
  border: 2px solid rgba(52, 152, 255, 0.55);
  color: #000;
}

/* --- Rounded Square Date Box --- */
.squared-box {
  width: 45px;
  height: 45px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  font-weight: 600;
  transition: 0.2s;
}

/* Date number centered */
.day-num {
  font-size: 1.2em;
  font-weight: bold;
  color: #51412b;
}

/* Selected outline */
.calendar-day.selected {
  outline: 2px solid #8452f9;
  border-radius: 12px;
}

/* Hover effect */
.squared-box:hover {
  background: rgba(0, 0, 0, 0.08);
  cursor: pointer;
}

.day-num {
  font-size: 1.2em;
  color: #51412b;
}
.apply-btn {
  width: 100%;
  background: #453421;
  color: #fff;
  border: none;
  border-radius: 18px;
  font-size: 1.1em;
  padding: 7px 0;
  margin-top: 11px;
}

.calendar {
  flex: 1;
  padding: 20px;
  border-radius: 18px;
  background: #f6f7fb;
}

.calendar-header {
  font-size: 1.2em;
  text-align: center;
  margin-bottom: 7px;
  font-weight: bold;
}

.todo-list {
  flex: 1;
  background: #f8eeff;
  border-radius: 17px;
  padding: 18px;
  min-width: 280px;
}

.subjects {
  margin-top: 20px;
  background: #e3fcf8;
  border-radius: 17px;
  padding: 16px;
}
.subject-block {
  margin-bottom: 19px;
  padding-left: 7px;
}
.subject-title {
  font-size: 1.1em;
  font-weight: bold;
}
.chapter-item {
  margin: 4px 0;
  font-size: .95em;
}

.rounded-input {
  border-radius: 9px;
  border: 1px solid #aaa;
  padding: 8px;
  margin-top: 8px;
  width: 90%;
}
.primary-btn, .transfer-btn {
  border: none;
  border-radius: 12px;
  background: #8452f9;
  color: #fff;
  margin-top: 7px;
  margin-right: 7px;
  padding: 7px 18px;
  font-size: 1em;
  cursor: pointer;
  box-shadow: 0 2px 5px #8452f933;
}
.transfer-btn {
  background: #49bdd3;
}
.done {
  opacity: .6;
  text-decoration: line-through;
}
